import Vue from 'vue'
import axios from '../axios'
import VueAxios from 'vue-axios'
// import qs from 'qs'
// import router from '../router'
import JsPDF from 'jspdf'
import 'jspdf-autotable'

Vue.use(VueAxios, axios)

const simulasis = {
  namespaced: true,
  state: {
    hasil: [],
    hasilSimulasiAN: [],
    hasilSimulasiAB: [],
    acSnackBar: false,
    notif: '',
    valid: true,
    dialog: false,
    loading: false,
    showAN: false,
    showAB: false,
    current: ''
  },
  actions: {
    loadDataHB ({ commit }, hb) {
      axios
        .get('judge_usulan/' + hb)
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            commit('SET_HASIL', resp.data)
            commit('SET_CURRENT', hb)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    simulate ({ commit }, hb) {
      commit('SET_LOADING', true)
      if (hb === '13') {
        axios
          .get('sim_usulan')
          .then(response => {
            const resp = response.data
            if (resp.success === true) {
              commit('SET_SIMULAN', resp.data)
              commit('SET_LOADING', false)
              console.log(resp.data)
            }
          })
          .finally(() => {
            commit('SET_LOADING', false)
          })
      } else {
        axios
          .get('sim_usulab')
          .then(response => {
            const resp = response.data
            if (resp.success === true) {
              commit('SET_SIMULAB', resp.data)
              commit('SET_LOADING', false)
            }
          })
          .finally(() => {
            commit('SET_LOADING', false)
          })
      }
    },
    printAN ({ state }) {
      const dataPrint = []
      for (let i = 0; i < state.hasilSimulasiAN.length; i++) {
        const entri = [(i + 1), [state.hasil[i].nama + '\nNIM : ' + state.hasil[i].nim], state.hasil[i].judulFinal, state.hasilSimulasiAN[i].nama_p1, state.hasilSimulasiAN[i].nama_p2, state.hasilSimulasiAN[i].nama_p3, state.hasilSimulasiAN[i].nama_p4]
        dataPrint.push(entri)
      }
      const doc = new JsPDF('landscape')
      doc.text('Hasil Simulasi Penentuan Dosen Pembimbing dan Penguji LA(D4-AN) dengan narSys', 14, 20)
      doc.autoTable({
        startY: 25,
        head: [['No', 'ID Mahasiswa', 'Judul', 'Pembimbing 1', 'Pembimbing 2', 'Penguji 1', 'Penguji 2']],
        headStyles: { textColor: 100, fillColor: [240, 230, 140] },
        body: dataPrint,
        columnStyles: { 0: { halign: 'right' }, 1: { fontStyle: 'bold' }, 2: { cellWidth: 100 } },
        theme: 'grid'
      })
      doc.save('Hasil_Simulasi_AN.pdf')
    },
    printAB ({ state }) {
      const dataPrint = []
      for (let i = 0; i < state.hasilSimulasiAB.length; i++) {
        const entri = [(i + 1), [state.hasil[i].nama + '\nNIM : ' + state.hasil[i].nim], state.hasil[i].judulFinal, state.hasilSimulasiAB[i].nama_p1, state.hasilSimulasiAB[i].nama_p2, state.hasilSimulasiAB[i].nama_p3]
        dataPrint.push(entri)
      }
      const doc = new JsPDF('landscape')
      doc.text('Hasil Simulasi Penentuan Dosen Pembimbing dan Penguji TA(D3-AB) dengan narSys', 14, 20)
      doc.autoTable({
        startY: 25,
        head: [['No', 'ID Mahasiswa', 'Judul', 'Pembimbing 1', 'Penguji 1', 'Penguji 2']],
        headStyles: { fillColor: [148, 0, 211] },
        body: dataPrint,
        columnStyles: { 0: { halign: 'right' }, 1: { fontStyle: 'bold' }, 2: { cellWidth: 100 } },
        theme: 'grid'
      })
      doc.save('Hasil_Simulasi_AB.pdf')
    },
    setSnackBar: ({ commit }, value) => {
      commit('SET_SNACKBAR', value)
    },
    setNotif: ({ commit }, value) => {
      commit('SET_NOTIF', value)
    },
    setValid: ({ commit }, value) => {
      commit('SET_VALID', value)
    },
    setDialog: ({ commit }, value) => {
      commit('SET_DIALOG', value)
    },
    setShowAN: ({ commit }, value) => {
      commit('SET_SHOW_AN', value)
    },
    setShowAB: ({ commit }, value) => {
      commit('SET_SHOW_AB', value)
    }
  },
  mutations: {
    SET_HASIL: (state, value) => {
      state.hasil = value
    },
    SET_SIMULAN: (state, value) => {
      state.hasilSimulasiAN = value
    },
    SET_SIMULAB: (state, value) => {
      state.hasilSimulasiAB = value
    },
    SET_SNACKBAR: (state, value) => {
      state.acSnackBar = value
    },
    SET_NOTIF: (state, value) => {
      state.notif = value
    },
    SET_VALID: (state, value) => {
      state.valid = value
    },
    SET_DIALOG: (state, value) => {
      state.dialog = value
    },
    SET_LOADING: (state, value) => {
      state.loading = value
    },
    SET_SHOW_AN: (state, value) => {
      state.showAN = value
    },
    SET_SHOW_AB: (state, value) => {
      state.showAB = value
    },
    SET_CURRENT: (state, value) => {
      state.current = value
    }
  }
}

export default simulasis
