import Vue from 'vue'
import axios from '../axios'
import VueAxios from 'vue-axios'
import qs from 'qs'

Vue.use(VueAxios, axios)

const profiles = {
  namespaced: true,
  state: {
    nip: '',
    idCriteria: '',
    idVariabel: '',
    score: '',
    listPendidikan: [],
    listSertifikasi: [],
    listPengalaman: [],
    listHomebase: [],
    listMetPen: [],
    rumpun: [],
    subRumpun: [],
    homebase: [],
    metPen: [],
    acSnackBar: false,
    notif: '',
    valid: true,
    validPen: true,
    validHom: true,
    validMet: true,
    dialog: false,
    pos: 1
  },
  actions: {
    loadRumpun ({ commit }) {
      axios
        .get('variabel/0')
        .then(response => {
          const resp = response.data
          // console.log(resp)
          commit('SET_RUMPUN', resp.data)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadSubRumpun ({ commit, state }, idRumpun) {
      const nip = localStorage.getItem('id')
      const idCriteria = state.pos
      axios
        .get('variabel/' + nip + '/criteria/' + idCriteria + '/kodeInduk/' + idRumpun)
        .then(response => {
          const resp = response.data
          commit('SET_BIDANG_ILMU', resp.data)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadHomebase ({ commit }) {
      axios
        .get('variabel/10')
        .then(response => {
          const resp = response.data
          commit('SET_HOMEBASE', resp.data)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadMetPen ({ commit, state }) {
      const nip = localStorage.getItem('id')
      const idCriteria = state.pos
      axios
        .get('variabel/' + nip + '/criteria/' + idCriteria + '/kodeInduk/20')
        .then(response => {
          const resp = response.data
          commit('SET_METPEN', resp.data)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadEntri ({ dispatch, commit }) {
      axios
        .get('scoring/' + localStorage.getItem('id'))
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            const kpendidikan = resp.data.filter(id => id.idCriteria === 1)
            if (kpendidikan.length > 0) {
              commit('SET_LIST_PENDIDIKAN', kpendidikan)
              dispatch('setValidPen', false)
            } else {
              dispatch('setValidPen', true)
            }
            const ksertifikasi = resp.data.filter(id => id.idCriteria === 2)
            if (ksertifikasi.length > 0) {
              commit('SET_LIST_SERTIFIKASI', ksertifikasi)
            }
            const kpengalaman = resp.data.filter(id => id.idCriteria === 3)
            if (kpengalaman.length > 0) {
              commit('SET_LIST_PENGALAMAN', kpengalaman)
            }
            const khomebase = resp.data.filter(id => id.idCriteria === 4)
            if (khomebase.length > 0) {
              commit('SET_LIST_HOMEBASE', khomebase)
              dispatch('setValidHom', false)
            } else {
              dispatch('setValidHom', true)
            }
            const kmetPen = resp.data.filter(id => id.idCriteria === 5)
            if (kmetPen.length > 0) {
              commit('SET_LIST_METPEN', kmetPen)
              if (kmetPen.length > 1) {
                dispatch('setValidMet', false)
              } else {
                dispatch('setValidMet', true)
              }
            }
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    setSnackBar: ({ commit }, value) => {
      commit('SET_SNACKBAR', value)
    },
    setNotif: ({ commit }, value) => {
      commit('SET_NOTIF', value)
    },
    setValid: ({ commit }, value) => {
      commit('SET_VALID', value)
    },
    setValidPen: ({ commit }, value) => {
      commit('SET_VALID_PEN', value)
    },
    setValidHom: ({ commit }, value) => {
      commit('SET_VALID_HOM', value)
    },
    setValidMet: ({ commit }, value) => {
      commit('SET_VALID_MET', value)
    },
    setDialog: ({ commit }, value) => {
      commit('SET_DIALOG', value)
    },
    setRoutePos: ({ commit }, value) => {
      commit('SET_ROUTE', value)
    },
    entriProfile ({ dispatch, commit, state }, profile) {
      const entri = profile
      entri.nip = localStorage.getItem('id')
      entri.idCriteria = state.pos
      axios
        .post('scoring', qs.stringify(entri))
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            dispatch('setSnackBar', true)
            dispatch('setNotif', resp.message)
            dispatch('setValid', false)
            if (state.pos === 1) {
              commit('SET_LIST_PENDIDIKAN_BARU', resp.data)
            } else if (state.pos === 2) {
              commit('SET_LIST_SERTIFIKASI_BARU', resp.data)
            } else if (state.pos === 3) {
              commit('SET_LIST_PENGALAMAN_BARU', resp.data)
            } else if (state.pos === 4) {
              commit('SET_LIST_HOMEBASE_BARU', resp.data)
            } else if (state.pos === 5) {
              commit('SET_LIST_METPEN_BARU', resp.data)
            } else {
            }
          } else {
            dispatch('setSnackBar', true)
            dispatch('setNotif', resp.message)
            dispatch('setValid', true)
          }
        })
    },
    deleteScoring ({ dispatch, commit }, scoring) {
      axios
        .get('scoringdel/' + scoring.id)
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            if (scoring.idCriteria === 1) {
              dispatch('setValidPen', true)
            } else if (scoring.idCriteria === 4) {
              dispatch('setValidHom', true)
            } else if (scoring.idCriteria === 5) {
              dispatch('setValidMet', true)
            } else {
            }
            commit('DELETE_SCORING', scoring)
          }
        })
    }
  },
  mutations: {
    SET_SNACKBAR: (state, value) => {
      state.acSnackBar = value
    },
    SET_NOTIF: (state, value) => {
      state.notif = value
    },
    SET_VALID: (state, value) => {
      state.valid = value
    },
    SET_VALID_PEN: (state, value) => {
      state.validPen = value
    },
    SET_VALID_HOM: (state, value) => {
      state.validHom = value
    },
    SET_VALID_MET: (state, value) => {
      state.validMet = value
    },
    SET_DIALOG: (state, value) => {
      state.dialog = value
    },
    SET_ROUTE: (state, value) => {
      state.pos = value
    },
    SET_RUMPUN: (state, value) => {
      state.rumpun = value
    },
    SET_BIDANG_ILMU: (state, value) => {
      state.subRumpun = value
    },
    SET_HOMEBASE: (state, value) => {
      state.homebase = value
    },
    SET_METPEN: (state, value) => {
      state.metPen = value
    },
    SET_LIST_PENDIDIKAN_BARU: (state, value) => {
      state.listPendidikan.push(value)
    },
    SET_LIST_PENDIDIKAN: (state, value) => {
      state.listPendidikan = value
    },
    SET_LIST_SERTIFIKASI_BARU: (state, value) => {
      state.listSertifikasi.push(value)
    },
    SET_LIST_SERTIFIKASI: (state, value) => {
      state.listSertifikasi = value
    },
    SET_LIST_PENGALAMAN_BARU: (state, value) => {
      state.listPengalaman.push(value)
    },
    SET_LIST_PENGALAMAN: (state, value) => {
      state.listPengalaman = value
    },
    SET_LIST_HOMEBASE_BARU: (state, value) => {
      state.listHomebase.push(value)
    },
    SET_LIST_HOMEBASE: (state, value) => {
      state.listHomebase = value
    },
    SET_LIST_METPEN_BARU: (state, value) => {
      state.listMetPen.push(value)
    },
    SET_LIST_METPEN: (state, value) => {
      state.listMetPen = value
    },
    DELETE_SCORING: (state, value) => {
      const idCriteria = value.idCriteria
      if (idCriteria === 1) {
        const index = state.listPendidikan.findIndex(list => list.id === value.id)
        state.listPendidikan.splice(index, 1)
      } else if (idCriteria === 2) {
        const index = state.listSertifikasi.findIndex(list => list.id === value.id)
        state.listSertifikasi.splice(index, 1)
      } else if (idCriteria === 3) {
        const index = state.listPengalaman.findIndex(list => list.id === value.id)
        state.listPengalaman.splice(index, 1)
      } else if (idCriteria === 5) {
        const index = state.listMetPen.findIndex(list => list.id === value.id)
        state.listMetPen.splice(index, 1)
      } else {
        const index = state.listHomebase.findIndex(list => list.id === value.id)
        state.listHomebase.splice(index, 1)
      }
    }
  }
}

export default profiles
