<template>
  <v-container fluid>
    <v-row>
      <v-col
      v-for="item in rekomendasis" :key="item.id" cols="12" md="4">
        <v-card>
          <v-card-title>
            <v-chip :class="color(item)">
              <v-icon left>mdi-account-circle-outline</v-icon>{{ item.nama }} ({{ item.nim }})
            </v-chip>
            <v-spacer/>
            <v-icon>{{ (item.bidangIlmu === null) ? 'mdi-checkbox-blank' : 'mdi-checkbox-marked' }}</v-icon>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-subtitle class="font-weight-bold">
            {{ item.judul }}
          </v-card-subtitle>
          <v-card-actions>
            <v-spacer/>
            <v-btn class="primary" text @click="loadDetailUsulan(item.id)">Review</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-dialog
      v-model="dialog"
      fullscreen
      transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="primary">
          <v-btn
            icon
            dark
            @click="dialog = false;loadMahasiswaPA()"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Entri Rekomendasi</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              depressed
              dark
              class="ma-0"
              color="primary"
              :disabled=!valid
              @click.prevent="entriRekomendasi();loadMahasiswaPA();clear()"
            >
            <v-icon left>mdi-content-save</v-icon>
              Simpan
            </v-btn>
          </v-toolbar-items>
          </v-toolbar>
          <v-row class="my-1 mx-1">
            <v-col cols="12" md="6">
              <v-card>
                <v-card-title class="text-justify text-subtitle-1 font-weight-bold">{{ rekomen.judul }}</v-card-title>
                <v-card-subtitle class="font-weight-medium">
                  Oleh : <v-chip :class="color(rekomen)"><v-icon left>mdi-account-circle-outline</v-icon>{{ rekomen.nama }} ({{ rekomen.nim }})</v-chip>
                  <v-btn class="orange darken-4 white--text ml-3" @click="loadRekapJudul(rekomen.judul)"><v-icon left>mdi-text-box-search</v-icon>Pemeriksaan Judul</v-btn>
                </v-card-subtitle>
                <v-card-text class="text-justify">{{ rekomen.abstraksi }}</v-card-text>
              </v-card>
            </v-col>
            <v-col cols="12" md="6">
              <v-card>
                <v-card-title class="font-weight-bold">Rekomendasi Anda</v-card-title>
                <v-container>
                  <v-form lazy-validation>
                    <v-card-text>
                      Berdasarkan data yang dientri dan asistensi yang dilakukan oleh mahasiswa maka judul dan abstraksi usulan dikatakan
                    <v-radio-group row v-model="rekomen.status">
                      <v-radio name="rekomen.status" label="Layak" value="Layak" @click="salinJudul(rekomen.judul)"></v-radio>
                      <v-radio name="rekomen.status" label="Belum" value="Belum" @click="salinJudul()"></v-radio>
                    </v-radio-group>
                    <v-textarea v-model="rekomen.rekomendasi" filled prepend-inner-icon="mdi-note-text" counter label=" Keterangan / Saran / Perbaikan" rows="5" row-height="30" auto-grow class="my-1" clearable clear-icon="mdi-close-circle"></v-textarea>
                    <v-textarea v-model="rekomen.judulFinal" filled prepend-inner-icon="mdi-format-title" counter label="Judul Final" class="my-1" row-height="10" auto-grow clearable clear-icon="mdi-close-circle"></v-textarea>
                    <v-divider/>
                    <v-select :items="kuota" item-text="ket" item-value="kode" class="mt-2" solo label="Bidang ilmu yang bersesuaian dengan judul" v-model="rekomen.bidangIlmu"></v-select>
                    <v-subheader class="font-weight-bold my-n5"><span class="red--text">Catatan</span>: setiap bidang ilmu hanya dibatasi 10 judul</v-subheader>
                    </v-card-text>
                  </v-form>
                </v-container>
              </v-card>
            </v-col>
          </v-row>
          <v-snackbar v-model="snackBar" centered timeout=-1 color="blue-grey darken-4" elevation="24">
              <v-icon>mdi-information mdi-48px</v-icon>{{ notif }}
              <v-btn left text color="red" class="font-weight-bold" @click="snackBar = false;valid=true">Tutup</v-btn>
          </v-snackbar>
        </v-card>
      </v-dialog>
      <v-dialog v-model="dialogAnak" scrollable max-width="500px">
        <v-card>
          <v-toolbar color="primary" dark><v-card-title>Judul serupa hingga tahun 2020</v-card-title>
          <v-spacer/>
          <v-btn
            icon
            dark
            @click="dialogAnak = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          </v-toolbar>
          <v-card-text>
            <v-list dense>
              <v-list-item v-for="item in listJudul" :key="item.id">
                <v-list-item-content>
                  <v-list-item-subtitle class="text-wrap" v-text="item.judul"></v-list-item-subtitle>
                  <v-list-item-subtitle class="text-wrap blue--text">Oleh : {{ item.nama }} Score : {{ item.score }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Rekomendasi',
  data: () => ({
    usul: {
      status: '',
      keterangan: '',
      judulRec: '',
      bidangIlmu: ''
    }
  }),
  computed: {
    ...mapState({
      rekomendasis: state => state.rekomendasi.rekomendasis,
      rekomen: state => state.rekomendasi.rekomendasi,
      kuota: state => state.rekomendasi.kuota,
      listJudul: state => state.rekomendasi.listJudul,
      acSnackBar: state => state.rekomendasi.acSnackBar,
      newNotif: state => state.rekomendasi.notif,
      acValid: state => state.rekomendasi.valid,
      acDialog: state => state.rekomendasi.dialog,
      acDialogAnak: state => state.rekomendasi.dialogAnak
    }),
    snackBar: {
      get () {
        return this.acSnackBar
      },
      set (value) {
        this.setSnackBar(value)
      }
    },
    notif: {
      get () {
        return this.newNotif
      },
      set (value) {
        this.setNotif(value)
      }
    },
    valid: {
      get () {
        return this.acValid
      },
      set (value) {
        this.setValid(value)
      }
    },
    dialog: {
      get () {
        return this.acDialog
      },
      set (value) {
        this.setDialog(value)
      }
    },
    dialogAnak: {
      get () {
        return this.acDialogAnak
      },
      set (value) {
        this.setDialogAnak(value)
      }
    }
  },
  mounted: function () {
    this.$store.dispatch('rekomendasi/loadMahasiswaPA')
    // this.$store.dispatch('rekomendasi/loadBidangIlmu')
  },
  methods: {
    ...mapActions({
      setSnackBar: 'rekomendasi/setSnackBar',
      setNotif: 'rekomendasi/setNotif',
      setValid: 'rekomendasi/setValid',
      setDialog: 'rekomendasi/setDialog',
      setDialogAnak: 'rekomendasi/setDialogAnak',
      loadMahasiswaPA: 'rekomendasi/loadMahasiswaPA'
    }),
    loadDetailUsulan (id) {
      this.$store.dispatch('rekomendasi/loadDetailMahasiswaPA', id)
    },
    loadRekapJudul (judul) {
      this.$store.dispatch('rekomendasi/loadRekapJudul', judul)
    },
    color (list) {
      const seleksi = String(list.nim).substring(5, 7)
      const warna = (seleksi === '13') ? 'lime lighten-3' : 'deep-purple lighten-1 white--text'
      return warna
    },
    entriRekomendasi () {
      const entri = this.rekomen
      // entri.id = this.rekomen.id
      this.$store.dispatch('rekomendasi/entriRekomendasi', entri)
    },
    salinJudul (judul) {
      if (judul !== null) {
        this.rekomen.judulFinal = judul
      } else {
        this.rekomen.judulFinal = ''
      }
    },
    clear () {
      this.usul.status = this.usul.keterangan = this.usul.judulRec = this.usul.bidangIlmu = ''
    }
  }
}
</script>
