<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>Pengaturan Password/Kata Sandi</v-card-title>
          <v-card-subtitle>Tidak ada batasan atau aturan terkait komposisi sebuah kata sandi / password</v-card-subtitle>
          <v-card-text>
            <v-text-field v-model="pengaturan.password" solo prepend-inner-icon="mdi-lock-alert" label="Password / Kata Sandi Baru" required :type="showPassP ? 'text' : 'password'" :append-icon="showPassP ? 'mdi-eye' : 'mdi-eye-off'" @click:append="showPassP = !showPassP"></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-btn class="primary" text @click.prevent="UbahPassword()">Simpan</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar v-model="snackBar" centered timeout="5000"  color="blue-grey darken-4" elevation="24">
      <v-icon>mdi-information mdi-48px</v-icon>{{ notif }}
      <v-btn left text color="red" class="font-weight-bold" @click="snackBar =  false">Tutup</v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Pengaturan',
  data: () => ({
    pengaturan: {
      password: ''
    },
    showPassP: false
  }),
  computed: {
    ...mapState({
      acSnackBar: state => state.pengaturan.acSnackBar,
      newNotif: state => state.pengaturan.notif
    }),
    snackBar: {
      get () {
        return this.acSnackBar
      },
      set (value) {
        this.setSnackBar(value)
      }
    },
    notif: {
      get () {
        return this.newNotif
      },
      set (value) {
        this.setNotif(value)
      }
    }
  },
  methods: {
    ...mapActions({
      setSnackBar: 'pengaturan/setSnackBar',
      setNotif: 'pengaturan/setNotif'
    }),
    UbahPassword () {
      this.$store.dispatch('pengaturan/ubahPassword', this.pengaturan)
    }
  }
}
</script>
