<template>
   <v-container fluid>
    <v-row class="mb-2">
      <v-btn color="primary" class="mr-2" @click="loadData('usulan');showP=false"><v-icon left>mdi-database-search-outline</v-icon>Data Usulan</v-btn>
      <v-btn color="orange accent-4" dark @click="loadData('profil');showU=false"><v-icon left>mdi-store-outline</v-icon>Data Profil Dosen</v-btn>
    </v-row>
    <v-divider class="my-2" />
    <v-row v-if="showU">
      <v-col cols="12">
        <v-card>
          <v-toolbar flat>
            <v-toolbar-title class="font-weight-bold">Data Usulan</v-toolbar-title>
            <v-divider class="mx-4" inset vertical />
            <v-text-field class="mt-6" v-model="searchUsulan" label="Masukkan kata kunci untuk pencarian" clearable solo></v-text-field>
          </v-toolbar>
          <v-card-text>
            <v-data-table :headers="this.headersUsulan" :search="this.searchUsulan" :items="hasilUsulan" item-key="usulid" class="elevation-5">
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-if="showP">
      <v-col cols="12">
        <v-toolbar flat>
          <v-toolbar-title class="font-weight-bold">Data Profil Dosen</v-toolbar-title>
        </v-toolbar>
      </v-col>
      <v-col v-for="(induk, index) in hasilProfil" :key="index" cols="12" md="3">
        <v-row class="mb-1 mx-2">
          <v-card>
            <v-toolbar class="orange accent-4" dark>
              <v-toolbar-title class="text-subtitle-1 font-weight-medium">{{ index }}</v-toolbar-title>
            </v-toolbar>
            <v-col cols="12" v-for="anak in induk" :key="anak.idCriteria">
              <v-card>
                <v-card-subtitle class="text-subtitle-1 font-weight-bold">{{ anak.kategori }}</v-card-subtitle>
                <v-divider />
                <v-card-text class="text-caption">{{ anak.detail }}</v-card-text>
              </v-card>
            </v-col>
          </v-card>
        </v-row>
      </v-col>
    </v-row>
   </v-container>
</template>

<script>
import { mapActions, mapState } from 'vuex'
export default {
  name: 'Penyesuaian',
  data () {
    return {
      searchUsulan: '',
      headersUsulan: [
        {
          text: 'Kode',
          sortable: true,
          value: 'usulid',
          cellClass: 'text-caption font-weight-black',
          class: 'white--text'
        },
        {
          text: 'NIM',
          value: 'nim',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'Mahasiswa',
          value: 'nama',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'Dosen PA',
          value: 'namaDosenPA',
          cellClass: 'text-caption font-weight-medium',
          class: 'white--text'
        },
        {
          text: 'Judul TA/LA',
          value: 'judul',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'Rekomendasi Judul',
          value: 'judulFinal',
          cellClass: 'text-caption font-weight-black',
          class: 'white--text'
        },
        {
          text: 'Rekomendasi Bidang Ilmu',
          value: 'ketBidang',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'Metode Penelitian',
          value: 'metPen',
          cellClass: 'text-caption font-weight-black',
          class: 'white--text'
        }
      ]
    }
  },
  computed: {
    ...mapState({
      hasilUsulan: state => state.penyesuaian.hasilUsulan,
      hasilProfil: state => state.penyesuaian.hasilProfil,
      acShowU: state => state.penyesuaian.showU,
      acShowP: state => state.penyesuaian.showP
    }),
    showU: {
      get () {
        return this.acShowU
      },
      set (value) {
        this.setShowU(value)
      }
    },
    showP: {
      get () {
        return this.acShowP
      },
      set (value) {
        this.setShowP(value)
      }
    }
  },
  methods: {
    ...mapActions({
      setShowU: 'penyesuaian/setShowU',
      setShowP: 'penyesuaian/setShowP'
    }),
    loadData (konten) {
      this.$store.dispatch('penyesuaian/loadData', konten)
    }
  }
}
</script>
<style>
  tbody tr:nth-of-type(even) {
    background-color: rgb(236, 236, 237);
  }
  tbody tr:nth-of-type(odd) {
    background-color: rgb(250 ,250, 250);
  }
  .v-data-table-header {
    background-color: #1976D2;
  }
</style>
