<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>Entri Profil Dosen</v-card-title>
          <v-card-subtitle class="text-justify">Profil dosen yang dientri akan dijadikan sebagai sumber penilaian/pemeringkatan. Dosen diminta untuk melakukan <i>self assessment</i> terhadap bidang ilmu yang bersesuaian dengan <b>latar belakang pendidikan, sertifikasi yang pernah diperoleh dan pengalaman mengajar ataupun sebagai praktisi.</b></v-card-subtitle>
          <v-card-text>
            <v-stepper v-model="pos" vertical non-linear>
                <v-stepper-step step="1" editable>Latar Pendidikan</v-stepper-step>
                <v-stepper-content step="1">
                  <v-card>
                    <v-card-subtitle>Merupakan bidang ilmu pendidikan terakhir.</v-card-subtitle>
                    <v-card-text>
                      <v-container>
                        <v-card v-for="dpendidikan in listPendidikan" :key="dpendidikan.id">
                          <v-card-title class="text-body-2 font-weight-bold">{{ dpendidikan.keterangan }}
                            <v-spacer/>
                            <v-btn color="error" fab x-small @click.prevent="deleteScoring(dpendidikan)"><v-icon>mdi-delete-alert</v-icon></v-btn>
                          </v-card-title>
                        </v-card>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn color="primary" @click="dialog = true;loadRumpun();pos = 1" :disabled=!validPen><v-icon left>mdi-note-plus</v-icon>Entri Data</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-stepper-content>
                <v-stepper-step step="2" editable>Sertifikasi</v-stepper-step>
                <v-stepper-content step="2">
                  <v-card>
                    <v-card-subtitle>Merupakan bidang ilmu sertifikasi <b>(dapat lebih dari 1)</b>.</v-card-subtitle>
                    <v-card-text>
                      <v-container>
                        <v-card v-for="dsertifikasi in listSertifikasi" :key="dsertifikasi.id">
                          <v-card-title class="text-body-2 font-weight-bold">{{ dsertifikasi.keterangan }}
                            <v-spacer/>
                            <v-btn color="error" fab x-small @click.prevent="deleteScoring(dsertifikasi)"><v-icon>mdi-delete-alert</v-icon></v-btn>
                          </v-card-title>
                        </v-card>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn color="primary" @click="dialog = true;loadRumpun()"><v-icon left>mdi-note-plus</v-icon>Entri Data</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-stepper-content>
                <v-stepper-step step="3" editable>Pengalaman</v-stepper-step>
                <v-stepper-content step="3">
                  <v-card>
                    <v-card-subtitle>Merupakan bidang ilmu pengalaman mengajar atau sebagai praktisi <b>(dapat lebih dari 1)</b>.</v-card-subtitle>
                    <v-card-text>
                      <v-container>
                        <v-card v-for="dpengalaman in listPengalaman" :key="dpengalaman.id">
                          <v-card-title class="text-body-2 font-weight-bold">{{ dpengalaman.keterangan }}
                            <v-spacer/>
                            <v-btn color="error" fab x-small @click.prevent="deleteScoring(dpengalaman)"><v-icon>mdi-delete-alert</v-icon></v-btn>
                          </v-card-title>
                        </v-card>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn color="primary" @click="dialog = true;loadRumpun()"><v-icon left>mdi-note-plus</v-icon>Entri Data</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-stepper-content>
                <v-stepper-step step="4" editable>Homebase</v-stepper-step>
                <v-stepper-content step="4">
                  <v-card>
                    <v-card-subtitle>Merupakan homebase dosen saat ini.</v-card-subtitle>
                    <v-card-text>
                      <v-container>
                        <v-card v-for="dhomebase in listHomebase" :key="dhomebase.id">
                          <v-card-title class="text-body-2 font-weight-bold">{{ dhomebase.keterangan }}
                            <v-spacer/>
                            <v-btn color="error" fab x-small @click.prevent="deleteScoring(dhomebase)"><v-icon>mdi-delete-alert</v-icon></v-btn>
                          </v-card-title>
                        </v-card>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn color="primary" @click="dialog = true;loadHomeBase()" :disabled=!validHom><v-icon left>mdi-note-plus</v-icon>Entri Data</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-stepper-content>
                <v-stepper-step step="5" editable>Metodologi Penelitian</v-stepper-step>
                <v-stepper-content step="5">
                  <v-card>
                    <v-card-subtitle>Merupakan penguasaan teknik analisis teoritis <b>(dapat memilih lebih dari 1)</b></v-card-subtitle>
                    <v-card-text>
                      <v-container>
                        <v-card v-for="dmetPen in listMetPen" :key="dmetPen.id">
                          <v-card-title class="text-body-2 font-weight-bold">{{ dmetPen.keterangan }}
                            <v-spacer/>
                            <v-btn color="error" fab x-small @click.prevent="deleteScoring(dmetPen)"><v-icon>mdi-delete-alert</v-icon></v-btn>
                          </v-card-title>
                        </v-card>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn color="primary" @click="dialog = true;loadMetPen()" :disabled=!validMet><v-icon left>mdi-note-plus</v-icon>Entri Data</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-stepper-content>
            </v-stepper>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="primary">
           <v-btn
            icon
            dark
            @click="dialog = false"
           >
            <v-icon>mdi-close</v-icon>
           </v-btn>
          <v-toolbar-title>Entri Data</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              depressed
              dark
              class="ma-0"
              color="primary"
              :disabled=!valid
              @click.prevent="entriProfile();loadEntri();clear()"
            >
            <v-icon left>mdi-content-save</v-icon>
              Simpan
            </v-btn>
          </v-toolbar-items>
          </v-toolbar>
          <v-row>
            <v-col cols="12">
              <v-card flat>
                <v-container>
                  <v-form lazy-validation>
                    <v-card-text v-if="pos==4">
                      <v-select v-model="profile.idVariabel" :items="homebase" item-text="keterangan" item-value="kode" solo label="Homebase"></v-select>
                    </v-card-text>
                    <v-card-text v-else-if="pos==5">
                      <v-select v-model="profile.idVariabel" :items="metPen" item-text="keterangan" item-value="kode" solo label="Metodologi Penelitian"></v-select>
                    </v-card-text>
                    <v-card-text v-else>
                      <v-select v-model="idRumpun" :items="rumpun" item-text="keterangan" item-value="kode" solo label="Sub Rumpun Ilmu" @change="loadSubRumpun()"></v-select>
                      <v-select v-model="profile.idVariabel" :items="subRumpun" item-text="keterangan" item-value="kode" solo label="Bidang Ilmu"></v-select>
                      <v-radio-group v-model="profile.score" row v-if="pos==3" label="Lamanya Pengalaman tersebut : ">
                        <v-radio label="kurang dari 3 tahun" value="50"></v-radio>
                        <v-radio label="lebih dari 3 tahun" value="100"></v-radio>
                      </v-radio-group>
                    </v-card-text>
                  </v-form>
                </v-container>
              </v-card>
            </v-col>
          </v-row>
          <v-snackbar v-model="snackBar" centered timeout=-1 color="blue-grey darken-4" elevation="24">
              <v-icon>mdi-information mdi-48px</v-icon>{{ notif }}
              <v-btn left text color="red" class="font-weight-bold" @click="snackBar = false;valid=true;dialog = ( valid ) ? false : true;loadEntri()">Tutup</v-btn>
          </v-snackbar>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Profile',
  data: () => ({
    idRumpun: '',
    profile: {
      idVariabel: '',
      score: '100'
    }
  }),
  computed: {
    ...mapState({
      rumpun: state => state.profile.rumpun,
      subRumpun: state => state.profile.subRumpun,
      homebase: state => state.profile.homebase,
      metPen: state => state.profile.metPen,
      listPendidikan: state => state.profile.listPendidikan,
      listSertifikasi: state => state.profile.listSertifikasi,
      listPengalaman: state => state.profile.listPengalaman,
      listHomebase: state => state.profile.listHomebase,
      listMetPen: state => state.profile.listMetPen,
      acSnackBar: state => state.profile.acSnackBar,
      newNotif: state => state.profile.notif,
      acValid: state => state.profile.valid,
      acValidPen: state => state.profile.validPen,
      acValidHom: state => state.profile.validHom,
      acValidMet: state => state.profile.validMet,
      acDialog: state => state.profile.dialog,
      acPos: state => state.profile.pos
    }),
    snackBar: {
      get () {
        return this.acSnackBar
      },
      set (value) {
        this.setSnackBar(value)
      }
    },
    notif: {
      get () {
        return this.newNotif
      },
      set (value) {
        this.setNotif(value)
      }
    },
    valid: {
      get () {
        return this.acValid
      },
      set (value) {
        this.setValid(value)
      }
    },
    validPen: {
      get () {
        return this.acValidPen
      },
      set (value) {
        this.setValidPen(value)
      }
    },
    validHom: {
      get () {
        return this.acValidHom
      },
      set (value) {
        this.setValidHom(value)
      }
    },
    validMet: {
      get () {
        return this.acValidMet
      },
      set (value) {
        this.setValidMet(value)
      }
    },
    dialog: {
      get () {
        return this.acDialog
      },
      set (value) {
        this.setDialog(value)
      }
    },
    pos: {
      get () {
        return this.acPos
      },
      set (value) {
        this.setPos(value)
      }
    }
  },
  mounted: function () {
    this.$store.dispatch('profile/loadEntri')
  },
  methods: {
    ...mapActions({
      setSnackBar: 'profile/setSnackBar',
      setNotif: 'profile/setNotif',
      setValid: 'profile/setValid',
      setValidPen: 'profile/setValidPen',
      setValidHom: 'profile/setValidHom',
      setValidMet: 'profile/setValidMet',
      setDialog: 'profile/setDialog',
      setPos: 'profile/setRoutePos',
      loadEntri: 'profile/loadEntri'
    }),
    loadRumpun () {
      this.$store.dispatch('profile/loadRumpun')
    },
    loadSubRumpun () {
      this.$store.dispatch('profile/loadSubRumpun', this.idRumpun)
    },
    loadHomeBase () {
      this.$store.dispatch('profile/loadHomebase')
    },
    loadMetPen () {
      this.$store.dispatch('profile/loadMetPen')
    },
    entriProfile () {
      this.$store.dispatch('profile/entriProfile', this.profile)
    },
    deleteScoring (id) {
      this.$store.dispatch('profile/deleteScoring', id)
      alert('Berhasil Dihapus !!!')
    },
    clear () {
      this.idRumpun = this.profile.idVariabel = ''
      this.profile.score = '100'
    }
  }
}
</script>
