import Vue from 'vue'
import axios from '../axios'
import VueAxios from 'vue-axios'
import qs from 'qs'

Vue.use(VueAxios, axios)

const rekomen = {
  namespaced: true,
  state: {
    rekomendasis: [],
    rekomendasi: [],
    kuota: [],
    listJudul: [],
    acSnackBar: false,
    notif: '',
    valid: true,
    dialog: false,
    dialogAnak: false
  },
  actions: {
    loadMahasiswaPA ({ dispatch, commit }) {
      axios
        .get('ras_usulan/' + localStorage.getItem('id'))
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            commit('SET_LOADMHSPA', resp.data)
          }
        })
    },
    loadDetailMahasiswaPA ({ dispatch, commit, state }, id) {
      const filter = state.rekomendasis.find(rekomendasi => rekomendasi.id === id)
      const hb = String(filter.nim).substring(5, 7)
      dispatch('setDialog', true)
      commit('SET_DETMHSPA', filter)
      axios
        .get('varkuota/' + hb)
        .then(response => {
          const resp = response.data
          commit('SET_KUOTA', resp.data)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadRekapJudul ({ dispatch, commit }, jud) {
      dispatch('setDialogAnak', true)
      const judul = jud.replace('.', '')
      axios
        .get('rekap/' + judul)
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            commit('SET_LIST_JUDUL', resp.data)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    setSnackBar: ({ commit }, value) => {
      commit('SET_SNACKBAR', value)
    },
    setNotif: ({ commit }, value) => {
      commit('SET_NOTIF', value)
    },
    setValid: ({ commit }, value) => {
      commit('SET_VALID', value)
    },
    setDialog: ({ commit }, value) => {
      commit('SET_DIALOG', value)
    },
    setDialogAnak: ({ commit }, value) => {
      commit('SET_DIALOG_ANAK', value)
    },
    entriRekomendasi ({ dispatch }, usul) {
      axios
        .post('usulan/' + usul.id, qs.stringify(usul))
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            dispatch('setValid', false)
          } else {
            dispatch('setValid', true)
          }
          dispatch('setSnackBar', true)
          dispatch('setNotif', resp.message)
        })
    }
  },
  mutations: {
    SET_LOADMHSPA: (state, value) => {
      state.rekomendasis = value
    },
    SET_DETMHSPA: (state, value) => {
      state.rekomendasi = value
    },
    SET_KUOTA: (state, value) => {
      state.kuota = value
    },
    SET_LIST_JUDUL: (state, value) => {
      state.listJudul = value
    },
    SET_SNACKBAR: (state, value) => {
      state.acSnackBar = value
    },
    SET_NOTIF: (state, value) => {
      state.notif = value
    },
    SET_VALID: (state, value) => {
      state.valid = value
    },
    SET_DIALOG: (state, value) => {
      state.dialog = value
    },
    SET_DIALOG_ANAK: (state, value) => {
      state.dialogAnak = value
    }
  }
}

export default rekomen
