<template>
  <v-container fluid class="my-4">
    <v-row justify="center" align="center">
      <v-col cols="12" md="5">
        <v-img
            :src="require('../assets/myznarsys_logo.png')"
            contain
            height="100"
            class="mb-2"
        />
        <v-card class="mx-auto">
          <v-card-title>
            Masuk ke akun anda
          </v-card-title>
          <v-container>
            <v-form ref="authform" v-model="valid" lazy-validation>
              <v-card-text>
                <v-text-field v-model="auth.id" solo prepend-inner-icon="mdi-account" label="NIP / NIM"></v-text-field>
                <v-text-field v-model="auth.password" solo prepend-inner-icon="mdi-form-textbox-password" label="Password / Kata Sandi" :type="showPassP ? 'text' : 'password'" :append-icon="showPassP ? 'mdi-eye' : 'mdi-eye-off'" @click:append="showPassP = !showPassP"></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-btn :loading="loading" block color="primary" @click.prevent="LoginUser()">Masuk</v-btn>
              </v-card-actions>
            </v-form>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar v-model="snackBar" centered timeout="5000"  color="blue-grey darken-4" elevation="24">
        <v-icon>mdi-information mdi-48px</v-icon>{{ notif }}
        <v-btn left text color="red" class="font-weight-bold" @click="snackBar = false;loading = false">Tutup</v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Auth',
  data: () => ({
    valid: true,
    auth: {
      id: '',
      password: ''
    },
    showPassP: false
  }),
  computed: {
    ...mapState({
      acSnackBar: state => state.auth.acSnackBar,
      newNotif: state => state.auth.notif,
      acLoading: state => state.auth.loading
    }),
    snackBar: {
      get () {
        return this.acSnackBar
      },
      set (value) {
        this.setSnackBar(value)
      }
    },
    notif: {
      get () {
        return this.newNotif
      },
      set (value) {
        this.setNotif(value)
      }
    },
    loading: {
      get () {
        return this.acLoading
      },
      set (value) {
        this.setLoading(value)
      }
    }
  },
  methods: {
    ...mapActions({
      setSnackBar: 'auth/setSnackBar',
      setNotif: 'auth/setNotif',
      setLoading: 'auth/setLoading'
    }),
    LoginUser () {
      this.$store.dispatch('auth/login', this.auth)
    }
  }
}
</script>
