import Vue from 'vue'
import axios from '../axios'
import VueAxios from 'vue-axios'
// import qs from 'qs'
// import router from '../router'

Vue.use(VueAxios, axios)

const penyesuaians = {
  namespaced: true,
  state: {
    hasilUsulan: [],
    hasilProfil: [],
    showU: false,
    showP: false
  },
  actions: {
    loadData ({ dispatch, commit }, konten) {
      if (konten === 'profil') {
        axios
          .get('scoring')
          .then(response => {
            const resp = response.data
            if (resp.success === true) {
              // kode group dalam js untuk menjadikan salah satu field sebagai index
              const groupBy = (array, key) => {
                return array.reduce((result, currentValue) => {
                  (result[currentValue[key]] = result[currentValue[key]] || []).push(currentValue)
                  return result
                }, {})
              }
              const groupbyNama = groupBy(resp.data, 'nama')
              commit('SET_HASIL_PROFIL', groupbyNama)
              dispatch('setShowP', true)
            }
          })
          .catch((error) => {
            console.log(error)
          })
      } else {
        axios
          .get('usulan')
          .then(response => {
            const resp = response.data
            // console.log(resp)
            if (resp.success === true) {
              commit('SET_HASIL_USULAN', resp.data)
              dispatch('setShowU', true)
            }
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },
    setShowU: ({ commit }, value) => {
      commit('SET_DATATABLE', value)
    },
    setShowP: ({ commit }, value) => {
      commit('SET_DATAPROFIL', value)
    }
  },
  mutations: {
    SET_HASIL_USULAN: (state, value) => {
      state.hasilUsulan = value
    },
    SET_HASIL_PROFIL: (state, value) => {
      state.hasilProfil = value
    },
    SET_DATATABLE: (state, value) => {
      state.showU = value
    },
    SET_DATAPROFIL: (state, value) => {
      state.showP = value
    }
  }
}

export default penyesuaians
