        </v-toolbar>
        <v-card-text>
          <v-data-table :headers="this.headersSimulasiAN" :items="hasilSimulasiAN" item-key="id" disable-pagination hide-default-footer class="elevation-5">
            <template v-slot:[`item.nama_p1`]="{ item }">
              <v-row>
                <v-col class="text-no-wrap">
                  <v-chip label small color="lime accent-3" class="font-weight-bold">{{ item.sc_p1 }}</v-chip>
                  {{ item.nama_p1 }}
                </v-col>
