      <v-card>
        <v-toolbar flat>
          <v-toolbar-title class="font-weight-bold">Hasil Simulasi AB</v-toolbar-title>
          <v-divider class="mx-4" inset vertical />
          <v-btn color="warning" class="mr-2" @click="printAB()"><v-icon left>mdi-printer</v-icon>Cetak</v-btn>
        </v-toolbar>
        <v-card-text>
          <v-data-table :headers="this.headersSimulasiAB" :items="hasilSimulasiAB" item-key="id" disable-pagination hide-default-footer class="elevation-5">
            <template v-slot:[`item.nama_p1`]="{ item }">
              <v-row>
                <v-col class="text-no-wrap">
                  <v-chip label small color="lime accent-3" class="font-weight-bold">{{ item.sco_p1 }}</v-chip>
                  {{ item.nama_p1 }}
                </v-col>
              </v-row>
