<script>
import { mapActions, mapState } from 'vuex'
export default {
  name: 'Simulasi',
  data () {
    return {
      headersUsulan: [
        {
          text: 'Kode',
          sortable: true,
          value: 'usulid',
          cellClass: 'text-caption font-weight-black',
          class: 'white--text'
        },
        {
          text: 'NIM',
          value: 'nim',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'Mahasiswa',
          value: 'nama',
          cellClass: 'text-caption',
          class: 'white--text'
        }
      ],
      headersSimulasiAN: [
        {
          text: 'Kode',
          value: 'id',
          sortable: false,
          cellClass: 'text-caption font-weight-black',
          class: 'white--text'
        },
        {
          text: 'p1',
          value: 'nama_p1',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'p2',
          value: 'nama_p2',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'p3',
          value: 'nama_p3',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'p4',
          value: 'nama_p4',
          cellClass: 'text-caption',
          class: 'white--text'
        }
      ],
      headersSimulasiAB: [
        {
          text: 'Kode',
          value: 'id',
          sortable: false,
          cellClass: 'text-caption font-weight-black',
          class: 'white--text'
        },
        {
          text: 'p1',
          value: 'nama_p1',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'p2',
          value: 'nama_p2',
          cellClass: 'text-caption',
          class: 'white--text'
        },
        {
          text: 'p3',
          value: 'nama_p3',
          cellClass: 'text-caption',
          class: 'white--text'
        }
      ]
    }
  },
  computed: {
    ...mapState({
      hasil: state => state.simulasi.hasil,
      hasilSimulasiAN: state => state.simulasi.hasilSimulasiAN,
      hasilSimulasiAB: state => state.simulasi.hasilSimulasiAB,
      acShowAN: state => state.simulasi.showAN,
      acShowAB: state => state.simulasi.showAB,
      current: state => state.simulasi.current,
      acLoading: state => state.simulasi.loading
    }),
    showAN: {
      get () {
        return this.acShowAN
      },
      set (value) {
        this.setShowAN(value)
      }
    },
    showAB: {
      get () {
        return this.acShowAB
      },
      set (value) {
        this.setShowAB(value)
      }
    },
    loading: {
      get () {
        return this.acLoading
      }
    }
  },
  methods: {
    ...mapActions({
      setShowAN: 'simulasi/setShowAN',
      setShowAB: 'simulasi/setShowAB'
    }),
    populate (hb) {
      this.$store.dispatch('simulasi/loadDataHB', hb)
    },
    simulasi (hbactive) {
      this.$store.dispatch('simulasi/simulate', hbactive)
    },
    printAN () {
      this.$store.dispatch('simulasi/printAN')
    },
    printAB () {
      this.$store.dispatch('simulasi/printAB')
    }
  }
}
</script>
