<template>
  <v-navigation-drawer v-model="drawer" absolute temporary left width="280">
    <v-toolbar>
      <v-img
        :src="require('../assets/myznarsys_logo.png')"
        contain
        height="100"
        class="my-2"
      />
    </v-toolbar>
    <v-divider></v-divider>
    <v-list>
      <v-list-item v-for="menu in selectedMenu" :key="menu.title" :to="menu.link" @click="toolBarTitle=menu.title">
        <v-list-item-icon>
          <v-icon>{{ menu.icon }}</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title class="font-weight-medium">{{ menu.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>
<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'c-sidebar',
  methods: {
    ...mapActions({
      setSideBar: 'nav/setSideBar',
      setTitle: 'nav/setTitle'
    })
  },
  computed: {
    ...mapState({
      menuDosen: state => state.nav.menuDosen,
      menuMahasiswa: state => state.nav.menuMahasiswa,
      menuKoordinator: state => state.nav.menuKoordinator,
      sideBar: state => state.nav.sideBar,
      title: state => state.nav.title
    }),
    drawer: {
      get () {
        return this.sideBar
      },
      set (value) {
        this.setSideBar(value)
      }
    },
    toolBarTitle: {
      get () {
        return this.title
      },
      set (value) {
        this.setTitle(value)
      }
    },
    selectedMenu: function () {
      let menu = null
      switch (localStorage.getItem('role')) {
        case 'koordinator': menu = this.menuKoordinator; break
        case 'dosen': menu = this.menuDosen; break
        default: menu = this.menuMahasiswa
      }
      return menu
    }
  }
}
</script>
