<template>
  <v-app>
    <c-header />
    <c-sidebar />
    <v-main>
      <v-container fluid>
        <component :is="activeComponent"></component>
      </v-container>
    </v-main>
    <c-footer />
  </v-app>
</template>

<script>
import CHeader from '../components/CHeader'
import CFooter from '../components/CFooter'
import CSidebar from '../components/CSidebar'
import Profil from '../components/Dosen/Profile.vue'
import Usulan from '../components/Mahasiswa/Usulan.vue'
import Pengaturan from '../components/Pengaturan.vue'
import Home from '../components/Home.vue'
import Rekomendasi from '../components/Dosen/Rekomendasi.vue'
import Penyesuaian from '../components/Koordinator/Penyesuaian.vue'
import Simulasi from '../components/Koordinator/Simulasi.vue'

export default {
  name: 'PostLogin',
  components: {
    CHeader,
    CFooter,
    CSidebar,
    home: Home,
    profil: Profil,
    usulan: Usulan,
    pengaturan: Pengaturan,
    rekomendasi: Rekomendasi,
    penyesuaian: Penyesuaian,
    simulasi: Simulasi
  },
  props: ['page'],
  data: () => ({
    //
  }),
  computed: {
    activeComponent: function () {
      let show = null
      switch (this.page) {
        case 'profil': show = 'profil'; break
        case 'usulan': show = 'usulan'; break
        case 'pengaturan': show = 'pengaturan'; break
        case 'rekomendasi': show = 'rekomendasi'; break
        case 'adjust': show = 'penyesuaian'; break
        case 'simulate': show = 'simulasi'; break
        default: show = 'home'
      }
      return show
    }
  }
}
</script>
