import Vue from 'vue'
import axios from '../axios'
import VueAxios from 'vue-axios'
import qs from 'qs'
import router from '../router'

Vue.use(VueAxios, axios)

const auths = {
  namespaced: true,
  state: {
    acSnackBar: false,
    notif: '',
    loading: false
  },
  actions: {
    setSnackBar: ({ commit }, value) => {
      commit('SET_SNACKBAR', value)
    },
    setNotif: ({ commit }, value) => {
      commit('SET_NOTIF', value)
    },
    setLoading: ({ commit }, value) => {
      commit('SET_LOADING', value)
    },
    login ({ dispatch }, auth) {
      dispatch('setLoading', true)
      axios
        .post('login', qs.stringify(auth))
        .then(response => {
          const resp = response.data
          // console.log(resp)
          if (resp.success === true) {
            localStorage.setItem('id', resp.data.user.id)
            localStorage.setItem('role', resp.data.user.role)
            localStorage.setItem('nama', resp.data.user.nama)
            localStorage.setItem('homebase', resp.data.user.homebase)
            localStorage.setItem('api_token', resp.data.api_token)
            dispatch('setLoading', false)
            router.push('/postlogin')
          } else {
            dispatch('setSnackBar', true)
            dispatch('setNotif', resp.message)
          }
        })
    }
  },
  mutations: {
    SET_SNACKBAR: (state, value) => {
      state.acSnackBar = value
    },
    SET_NOTIF: (state, value) => {
      state.notif = value
    },
    SET_LOADING: (state, value) => {
      state.loading = value
    }
  }
}

export default auths
