<template>
  <v-container fluid>
    <v-row>
      <v-btn color="primary" @click="dialog = true" :disabled=!valid><v-icon left>mdi-note-plus</v-icon>Buat Usulan</v-btn>
    </v-row>
    <v-row justify="center">
      <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
      >
        <v-card>
          <v-toolbar dark color="primary">
           <v-btn
            icon
            dark
            @click="dialog = false"
           >
            <v-icon>mdi-close</v-icon>
           </v-btn>
          <v-toolbar-title>Entri Usulan</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              @click.prevent="EntriUsulan()"
              depressed
              dark
              class="ma-0"
              color="primary"
              :disabled=!valid
              :loading=loading
            >
            <v-icon left>mdi-content-save</v-icon>
              Simpan
            </v-btn>
          </v-toolbar-items>
          </v-toolbar>
          <v-row>
            <v-col cols="12">
              <v-card flat>
                <v-card-title>
                Isi dengan benar dan jabarkan usulan Tugas Akhir/Laporan Akhir anda dengan padat dan jelas.
                </v-card-title>
                <v-card-subtitle>
                Abstraksi berisi kejadian/peristiwa/kondisi yang kuat/jelas/terukur yang melatarbelakangi pemilihan judul. Pastikan judul yang anda entri <b>BUKAN</b> merupakan judul yang sudah pernah diangkat sebagai judul TA/LA.
                </v-card-subtitle>
                <v-container>
                  <v-form ref="usulanform" lazy-validation>
                    <v-card-text>
                      <v-select v-model="usul.nipPA" :items="dosenPA" item-text="nama" item-value="id" solo label="Dosen Pembimbing Akademik" required></v-select>
                      <v-text-field v-model="usul.judul" solo prepend-inner-icon="mdi-format-title" label="Judul" required></v-text-field>
                      <v-textarea v-model="usul.abstraksi" solo prepend-inner-icon="mdi-note-text" counter label="Abstraksi / Ringkasan" required rows="7"></v-textarea>
                    </v-card-text>
                  </v-form>
                </v-container>
              </v-card>
            </v-col>
          </v-row>
        </v-card>
      </v-dialog>
      <v-snackbar v-model="snackBar" centered timeout="5000"  color="blue-grey darken-4" elevation="24">
        <v-icon>mdi-information mdi-48px</v-icon>{{ notif }}
        <v-btn left text color="red" class="font-weight-bold" @click="snackBar =  false;dialog = ( valid ) ? true : false">Tutup</v-btn>
      </v-snackbar>
    </v-row>
    <v-row class="my-5">
      <v-col cols="12" md="6">
        <v-card v-for="usulan in hasilUsulan" :key="usulan.id">
          <v-toolbar class="primary" dark>
             <v-toolbar-title class="font-weight-bold">Usulan Anda</v-toolbar-title>
          </v-toolbar>
          <v-card-subtitle class="font-weight-medium text-subtitle-1">{{ usulan.judul }}</v-card-subtitle>
          <v-card-text class="text-justify">{{ usulan.abstraksi }}</v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" md="6">
        <v-card v-for="usulan in hasilUsulan" :key="usulan.id">
          <v-toolbar class="pink accent-3" dark>
             <v-toolbar-title class="font-weight-bold">Hasil Review Atas Usulan Anda</v-toolbar-title>
          </v-toolbar>
          <v-chip class="mt-4 ml-2 blue-grey darken-2" dark label>
            <v-icon left>mdi-comment-arrow-right</v-icon><v-card-subtitle class="font-weight-medium text-subtitle-1 white--text">Kelayakan Judul</v-card-subtitle>
          </v-chip>
          <v-card-text class="text-subtitle-2 font-weight-bold text-uppercase red--text">{{ usulan.status }}</v-card-text>
          <v-chip class="mt-2 ml-2 blue-grey darken-2" dark label>
            <v-icon left>mdi-comment-arrow-right</v-icon><v-card-subtitle class="font-weight-medium text-subtitle-1 white--text">Keterangan / Saran dari Dosen PA</v-card-subtitle>
          </v-chip>
          <v-card-text class="text-justify">{{ usulan.rekomendasi }}</v-card-text>
          <v-chip class="mt-2 ml-2 blue-grey darken-2" dark label>
            <v-icon left>mdi-comment-arrow-right</v-icon><v-card-subtitle class="font-weight-medium text-subtitle-1 white--text">Judul yang disarankan</v-card-subtitle>
          </v-chip>
          <v-card-text class="text-justify font-weight-bold">{{ usulan.judulFinal }}</v-card-text>
          <v-divider class="mt-2"/>
          <v-card-subtitle>Berdasarkan judul yang disarankan, <b>pendekatan / metode penelitian</b> yang akan anda pergunakan dalam TA/LA </v-card-subtitle>
          <v-card-text class="font-weight-bold">
            <v-radio-group row v-model="usulan.metPen">
              <v-radio name="usulan.metPen" label="KUALITATIF" value=1></v-radio>
              <v-radio name="usulan.metPen" label="KUANTITATIF" value=2></v-radio>
            </v-radio-group>
            <v-btn class="primary" dark text :loading=loading :disabled="(usulan.judulFinal===null)? true : false" @click.prevent="EntriMetPen()">Entri/Update Data</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Usulan',
  data: () => ({
    usul: {
      nipPA: '',
      judul: '',
      abstraksi: ''
    }
  }),
  computed: {
    ...mapState({
      dosenPA: state => state.usulan.dosenPA,
      hasilUsulan: state => state.usulan.hasilUsulan,
      acSnackBar: state => state.usulan.acSnackBar,
      newNotif: state => state.usulan.notif,
      acValid: state => state.usulan.valid,
      acDialog: state => state.usulan.dialog,
      acLoading: state => state.usulan.loading
    }),
    snackBar: {
      get () {
        return this.acSnackBar
      },
      set (value) {
        this.setSnackBar(value)
      }
    },
    notif: {
      get () {
        return this.newNotif
      },
      set (value) {
        this.setNotif(value)
      }
    },
    valid: {
      get () {
        return this.acValid
      },
      set (value) {
        this.setValid(value)
      }
    },
    dialog: {
      get () {
        return this.acDialog
      },
      set (value) {
        this.setDialog(value)
      }
    },
    loading: {
      get () {
        return this.acLoading
      }
    }
  },
  mounted: function () {
    this.$store.dispatch('usulan/loadDosenPA')
    this.$store.dispatch('usulan/loadUsulan')
  },
  methods: {
    ...mapActions({
      setSnackBar: 'usulan/setSnackBar',
      setNotif: 'usulan/setNotif',
      setValid: 'usulan/setValid',
      setDialog: 'usulan/setDialog'
    }),
    EntriUsulan () {
      this.$store.dispatch('usulan/entriUsulan', this.usul)
    },
    EntriMetPen () {
      const entri = this.hasilUsulan
      this.$store.dispatch('usulan/entriMetPen', entri)
    }
  }
}
</script>
