import Vue from 'vue'
import VueRouter from 'vue-router'
import Auth from '../components/Auth.vue'
import PostLogin from '../views/PostLogin.vue'
import Profil from '../components/Dosen/Profile.vue'
import Usulan from '../components/Mahasiswa/Usulan.vue'
import Pengaturan from '../components/Pengaturan.vue'
import Home from '../components/Home.vue'
import Rekomendasi from '../components/Dosen/Rekomendasi.vue'
import Penyesuaian from '../components/Koordinator/Penyesuaian.vue'
import Simulasi from '../components/Koordinator/Simulasi.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Auth',
    component: Auth,
    meta: {
      title: 'NarSys.::.AB-Polnep',
      metaTags: [
        {
          name: 'description',
          content: 'narSys merupakan sebuah Sistem Pendukung Keputusan untuk penentuan dosen pembimbing dan dosen penguji TA/LA dengan metode Best Worst Method'
        },
        {
          property: 'og:description',
          content: 'narSys merupakan sebuah Sistem Pendukung Keputusan untuk penentuan dosen pembimbing dan dosen penguji TA/LA dengan metode Best Worst Method'
        }
      ]
    }
  },
  {
    path: '/postlogin',
    name: 'PostLogin',
    component: PostLogin
  },
  {
    path: '/postlogin/:page',
    name: 'ActivePostLogin',
    component: PostLogin,
    props: true,
    children: [
      { path: 'profil', component: Profil },
      { path: 'usulan', component: Usulan },
      { path: 'pengaturan', component: Pengaturan },
      { path: 'home', component: Home },
      { path: 'rekomendasi', component: Rekomendasi },
      { path: 'adjust', component: Penyesuaian },
      { path: 'simulate', component: Simulasi }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
