import Vue from 'vue'
import axios from '../axios'
import VueAxios from 'vue-axios'
import qs from 'qs'
// import router from '../router'

Vue.use(VueAxios, axios)

const usulans = {
  namespaced: true,
  state: {
    dosenPA: [],
    hasilUsulan: [],
    newUsulan: [],
    acSnackBar: false,
    notif: '',
    valid: true,
    validm: true,
    dialog: false,
    loading: false
  },
  actions: {
    loadDosenPA ({ commit }) {
      axios
        .get('user/dosen')
        .then(response => {
          const resp = response.data
          // console.log(resp)
          commit('SET_DOSENPA', resp.data)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadUsulan ({ dispatch, commit }) {
      axios
        .get('lihat_usulan/' + localStorage.getItem('id'))
        .then(response => {
          const resp = response.data
          // console.log(resp)
          if (resp.success === true) {
            commit('SET_HASIL_USULAN', resp.data)
            dispatch('setValid', false)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    setSnackBar: ({ commit }, value) => {
      commit('SET_SNACKBAR', value)
    },
    setNotif: ({ commit }, value) => {
      commit('SET_NOTIF', value)
    },
    setValid: ({ commit }, value) => {
      commit('SET_VALID', value)
    },
    setDialog: ({ commit }, value) => {
      commit('SET_DIALOG', value)
    },
    entriUsulan ({ dispatch, commit }, usulan) {
      const entri = usulan
      entri.nim = localStorage.getItem('id')
      axios
        .post('usulan', qs.stringify(entri))
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            dispatch('setSnackBar', true)
            dispatch('setNotif', resp.message)
            dispatch('setValid', false)
            commit('SET_USULAN_BARU', resp.data)
            commit('SET_LOADING', true)
          } else {
            dispatch('setSnackBar', true)
            dispatch('setNotif', resp.message)
            dispatch('setValid', true)
          }
        })
        .finally(() => {
          commit('SET_LOADING', false)
        })
    },
    entriMetPen ({ dispatch, commit }, usulan) {
      commit('SET_LOADING', true)
      const metPen = usulan[0].metPen
      const idUsulan = usulan[0].id
      // console.log(idUsulan + '-' + metPen)
      axios
        .post('usulanm/' + idUsulan, qs.stringify({
          metPen: metPen
        }))
        .then(response => {
          const resp = response.data
          if (resp.success === true) {
            dispatch('setSnackBar', true)
            dispatch('setNotif', resp.message)
            commit('SET_LOADING', false)
          } else {
            dispatch('setSnackBar', true)
            dispatch('setNotif', resp.message)
            commit('SET_LOADING', false)
          }
        })
    }
  },
  mutations: {
    SET_DOSENPA: (state, value) => {
      state.dosenPA = value
    },
    SET_USULAN_BARU: (state, value) => {
      state.hasilUsulan.push(value)
    },
    SET_HASIL_USULAN: (state, value) => {
      state.hasilUsulan = value
    },
    SET_SNACKBAR: (state, value) => {
      state.acSnackBar = value
    },
    SET_NOTIF: (state, value) => {
      state.notif = value
    },
    SET_VALID: (state, value) => {
      state.valid = value
    },
    SET_DIALOG: (state, value) => {
      state.dialog = value
    },
    SET_LOADING: (state, value) => {
      state.loading = value
    }
  }
}

export default usulans
